const duration = 2000;
import service from '../../service.js';
import http from '@/components/utils/http.js';
import { mapState, mapMutations } from 'vuex';

export default {
	components: {},
	data() {
		return {
			loginMode: 1,
			usernameType: 'text',
			codeBut: '获取验证码',
			codeClick: true,
			mobile: '',
			password: '',
			repassword: '',
			code: '',
			backpage: '../index/home'
		};
	},
	computed: mapState(['forcedLogin']),
	methods: {
		...mapMutations(['login']),

		// 手机号输入
		mobileInput(e) {
			this.mobile = e.detail.value;
		},
		// 密码输入
		passwordInpur(e) {
			this.password = e.detail.value;
		},
		// 重复密码输入
		repasswordInpur(e) {
			this.repassword = e.detail.value;
		},
		// 验证码输入
		codeInput(e) {
			this.code = e.detail.value;
		},
		// 获取验证码
		getCode() {
			var _that = this;

			if (_that.codeClick) {
				if (_that.mobile[0] != 1 || _that.mobile.length != 11) {
					this.$api.msg('请输入正确的手机号');
				} else {
					_that.codeClick = false;
					// 发送短信
					_that._sendmsm(_that.mobile);
					// 倒计时开始
				}
			}
		},
		// 点击登录
		regUp() {
			var _that = this;
			if (_that.mobile == '') {
				this.$api.msg('请输入手机号码');
			} else if (_that.password == '') {
				this.$api.msg('请输入密码');
			} else if (_that.repassword != _that.password) {
				this.$api.msg('两次密码输入不一致');
			} else if (_that.code == '') {
				this.$api.msg('请输入验证码');
			} else {
				const data = {
					mobile: _that.mobile,
					password: _that.password,
					repassword: _that.repassword,
					code: _that.code
				};
				this._regrequest(data);
			}
		},

		_regrequest(userdata) {
			let opts = {
				url: '/userApi/reg/',
				method: 'post'
			};
			let param = userdata;
			http.httpRequest(opts, param).then(
				res => {
					//打印请求返回的数据
					if (res.data['code'] == 0) {
						this.userinfo = res.data['userinfo'];
						this.login(this.userinfo);
						uni.navigateTo({
							url: this.backpage
						});
					} else {
						this.$api.msg(res.data.msg);
					}
				},
				error => {
					console.log(error);
				}
			);
		},
		_sendmsm(mobile) {
			var _that = this;
			let opts = {
				url: '/CommonApi/sendmsm/',
				method: 'post'
			};
			let param = { mobile: mobile, sendtype: 'reg' };
			http.httpRequest(opts, param).then(
				res => {
					//打印请求返回的数据
					if (res.data['code'] == 0) {
						var daojishi = 60;
						var xianshi = setInterval(function() {
							_that.codeBut = '重新发送' + daojishi;
							daojishi--;
							if (daojishi <= 0) {
								_that.codeBut = '重新发送';
								_that.codeClick = true;
								clearInterval(xianshi);
							}
						}, 1000);
					} else {
						this.$api.msg(res.data.msg);
					}
				},
				error => {
					console.log(error);
					uni.showModal({
						content: err.errMsg,
						showCancel: false
					});
				},
				complete => {
					this.loading = false;
				}
			);
		}
	},
	onReady() {},
	onLoad(options) {
		if (options.backpage) {
			this.backpage = options.backpage;
		}
	}
};
